.about-content {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 40px;
  max-width: 900px;
  width: 100%;
  flex-wrap: wrap;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
}

.about-image img {
  width: 300px;
  height: 300px;
  object-fit: cover;
  border-radius: 50%;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
}

.about-text {
  flex: 1;
  color: #ffffff;
  font-size: 18px;
  line-height: 1.6;
}

.about-text h1 {
  font-size: 36px;
  color: #2a2a2a;
  margin-bottom: 20px;
}

.about-text p {
  font-size: 15px;
  margin-bottom: 15px;
}
.about-text h1 {
  color: white;
}
.about-text a {
  color: #fefefe;
  text-decoration: underline;
  font-weight: bold;
}
