.blog-post {
    max-width: 800px;
    margin: 50px auto; 
    padding: 120px 20px 20px;
  }
  
  .back-link {
    display: inline-block;
    margin-bottom: 20px;
    text-decoration: none;
    color: #007bff;
    font-size: 14.4px;
  }
  
  .back-link:hover {
    text-decoration: underline;
  }
  
  .post-title {
    font-size: 45px;
    color: #333;
    margin-bottom: 10px;
  }
  
  .post-meta {
    font-size: 18px;
    color: #666;
    margin-bottom: 20px;
  }
  
  .post-content {
    font-size: 21.6px;
    line-height: 1.8;
    color: #444;
  }
  