body {
  overflow-y: scroll;
}
.project-tags {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 5px;
  margin-top: 0px;
}

.project-tag {
  background-color: #f0f0f0;
  border-radius: 12px;
  padding: 5px 10px;
  font-size: 12px;
  font-weight: bold;
  color: #333;
}
.project-title {
  color: white;
  font-size: 40px;
  font-weight: 500;
  font-family: Tahoma;
}
.project-link a {
  color: rgb(255, 255, 255);
  text-decoration: none;
  font-weight: 500;
  font-size: 20px;
  margin-bottom: 5px;
  line-height: 1.2;
}

.project-container {
  background-color: linear-gradient(
    to right,
    rgb(235, 243, 247),
    rgb(109, 123, 158)
  );
  border-radius: 8px;
  padding: 15px;
  width: 400px;
  text-align: center;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0);
  transition:
    transform 0.2s,
    box-shadow 0.2s;
  z-index: 10;
}

.project-link {
  margin-top: 15px;
  margin-bottom: 0px;
  color: white;
  font-size: 20px;
}

.project-description {
  margin-top: 5px;
  color: white;
  font-size: 15px;
}
